* {
  margin: 0;
  padding: 0;
}

.h-100vh {
  height: 100vh;
}

#header-container {
  height: 50px;
  padding: 5px 20px;
  background-color: rgb(136, 34, 34);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
}

#loader-container {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}

#loader-container p {
  margin: 0;
  font-size: 2em;
  color: white;
}
